import React, { useState } from 'react';
import { Collapse, Button, CardBody, Card } from 'reactstrap';
import { FaStar, FaQuestionCircle, FaRegQuestionCircle } from 'react-icons/fa'


const Howtoplay = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div class="text-right">    
      <Button color="success" onClick={toggle} style={{ marginBottom: '1.5rem' }}>How To Play <FaQuestionCircle/> </Button>
      <Collapse isOpen={isOpen}>
        <Card>
          <CardBody>
          <div class="text-left"> 
          1. 2 players take alternate turns (starting with the player using the Red <FaStar size = "3vw" color = "#f56363" />).<br/>
          2. In each turn, the player gets a chance to drop a single star in any of the columns on the board.<br/>
          and the star will be dropped in the lowest available row in the column chosen.<br/>
          3. The player has to aim to make a horizontal, vertical or diagonal line of 4-connected stars on the board and must also prevent the opponent player from doing so.<br/>
          4. The player who first succeeds in forming the 4-connected stars line shall be declared as the winner.<br/>
           And Oh! Don't forget to have fun! :)
          </div>
          </CardBody>
        </Card>
      </Collapse>
    </div>
  );
}

export default Howtoplay;